$chrome: #cba05d;
$red: #ed7253;
@font-face {
	font-family: icons;
	src: url("fonts/fa/fa.eot");
    src: url("fonts/fa/fa.svg") format("svg"), url("fonts/fa/fa.woff2") format("woff2"), url("fonts/fa/fa.woff") format("woff"), url("fonts/fa/fa.ttf") format("truetype");
    font-weight: 400;
}
body {
	font-size: 16px;
}
ul {
	margin: 0;
	padding: 0;
	list-style: none;
}
.fixed-bar {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1000;
}
#top-line {
	position: relative;
	z-index: 1;
	height: 6px;
	width: 100%;
	background: $chrome;
	background: -moz-linear-gradient(135deg, #CDA25F 0%, #A7804D 38%, #CDA25F 73%, #A7804D 100%);;
    background: -webkit-linear-gradient(135deg, #CDA25F 0%, #A7804D 38%, #CDA25F 73%, #A7804D 100%);;
    background: -ms-linear-gradient(135deg, #CDA25F 0%, #A7804D 38%, #CDA25F 73%, #A7804D 100%);;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.15);
}
#header-content {
	position: relative;
	height: 110px;
	background-color: rgba(0,0,0,.75);
}
.soc-networks {
	margin-top: 40px;
	float: left;
	ul {
		li {
			float: left;
			font-size: 18px;
			&:not(:first-child) {
				margin-left: 15px;
			}
			a {
				color: #808080;
				&:hover {
					color: $chrome;
				}
			}
		}
	}
}
#main-nav {
	float: right;
	li a {
		font-family: "Orbitron",sans-serif;
		color: #999;
	}
	ul {
		> li {
			&:not(:first-child) {
				margin-left: 15px;
			}
		}
	}
	ul {
		li {
			float: left;
			text-transform: uppercase;
		}
	}
	li.bg-li {
		background-position:  0;
		&:hover,
		&.open {
			> a {
				color: $chrome;
			}
		}
	}
	li.bg-li > a {
		display: block;
		margin-top: 22px;
		padding-top: 50px;
		background-repeat: no-repeat;
		background-position: center top;
		background-size: 36px 43px;
		font-size: 13px;
	}
	li.store {
		&:hover > a,
		&.open > a {
			background-image: url('data:image/png;base64,');
		}
	}
	li.store > a {
		background-size: 33px 43px;
		background-image: url('data:image/png;base64,');
	}
	li.cart a {
		background-image: url('data:image/png;base64,');
		&:hover {
			background-image: url('data:image/png;base64,');
		}
	}
	li.login a {
		background-image: url('data:image/png;base64,');
		&:hover {
			background-image: url('data:image/png;base64,');
		}
	}
	li.cart {
		position: relative;
		.count {
			position: absolute;
			width: 18px;
			height: 18px;
			right: 0;
			bottom: 15px;
			border-radius: 50%;
			font-family: "Titillium Web",sans-serif;
			font-weight: 700;
			line-height: 20px;
			text-align: center;
			background-color: #ffe09c;
			color: #4c4c4c;
		}
	}
	ul li.dd-menu {
		position: relative;
		margin-left: 30px;
		width: 75px;
		height: 110px;
		padding-top: 27px;
		cursor: pointer;
		background-color: $chrome;
		text-align: center;
		&.store {
			padding-top: 0;
			background-color: transparent;
			&:hover,
			ul {
				padding: 0;
				background-color: #252526;
			}
			ul {
				display: block;
				visibility: hidden;
				opacity: 0;
				-webkit-transition: all .3s ease;
				transition: all .5s ease;
			}
			&:hover {
				ul {
					opacity: 1;
					visibility: visible;
				}
			}
			li {
				&:not(:last-child) {
					border-bottom: 1px solid #000;
				}
				
				&:not(:first-child) {
					border-top: 1px solid #2a2d33;
				}
				> a {
					position: relative;
					padding: 20px 40px 15px 20px;
					color: #FFE1AD;
					&:hover {
						background-color: $chrome;
						i {
							color: #795b34;
						}
					}
					span {
						font-family: "Titillium Web",sans-serif;
						font-size: .75em;
						line-height: 1;
					}
					i {
						top: 50%;
						margin: 0;
						margin-top: -10px;
						position: absolute;
						right: 20px;
						color: #1a1a1a;
					}
				}
			}
		}
		ul {
			display: none;
			padding-top: 15px;
			padding-bottom: 15px;
			text-align: left;
			position: absolute;
			right: 0;
			top: 100%;
			width: 250px;
			background-color: $chrome;
			&.active {
				display: block;
			}
			li {
				margin: 0;
				float: none;
				a {
					i {
						width: 20px;
						text-align: center;
						margin-left: 10px;
						margin-right: 10px;
						font-size: 1.25em;
					}
					display: block;
					width: 100%;
					color: #fff;
					padding: 7px 10px;
					&:hover{
						color: #FFE1AD;
						background-color: rgba(0,0,0,.15);
					}
					
				}
			}
		}
		> span {
			display: inline-block;
			height: 4px;
			width: 40px;
			background-color: #fad9a0;
			border-top: 1px solid #9e7742;
			border-radius: 2px;
			&:nth-child(2) {
				position: relative;
				top: -10px;
			}
			&:nth-child(3) {
				position: relative;
				top: -20px;
			}
		}
	}
}
#logo {
	position: absolute;
	top: 10px;
	left: 50%;
	margin-left: -45px;
	a {
		display: block;
		background-image: url('img/logo-sabermach.svg');
		background-size: 90px 90px;
		width: 90px;
		height: 90px;
	}
}
.store .content {
	display: flex;
	.col-left, .col-right {
		padding-top: 120px;
	}
}

.btn {
	border: 0;
	background: #0a0a0a;
	font-family: $font__heading;
	text-transform: uppercase;
	color: $color__mid;
	clip-path: polygon(7% 0, 93% 0%, 100% 50%, 93% 100%, 7% 100%, 0% 50%);
	padding: 15px 25px;
	font-size: 1.2rem;
	font-weight: bold;
	position: relative;
	display: inline-block;
	.icon {
		width: 20px;
		fill: $color__mid;
		margin: 0 5px;
		vertical-align: -2px;
	}
	&:hover {
		background: linear-gradient(0deg, $color__primary-dark 0%, $color__primary-dark 49%, $color__primary 50%);
		color: #fff;
		.icon {
			fill: #fff;
		}
	}
	&--transparent {
		background: #444;
		color: $color__mid;
		padding: 15px 45px;
		.border {
			position: absolute;
				top: 3px;
				right: 3px;
				bottom: 3px;
				left: 3px;
			background: #222;
			z-index: 0;
			clip-path: polygon(7% 0, 93% 0%, 100% 50%, 93% 100%, 7% 100%, 0% 50%);
		}
		.text {
			position: relative;
			z-index: 100;
		}
	}
	&:disabled {
		background: #272727;
		color: #444;
		.icon {
			fill: #444;
		}
	}
}
.button-wrap {
	display: inline-block;
	margin: 0 15px 20px;
	text-align: center;
	.label {
		display: block;
		font-size: 0.9rem;
		margin: 5px 0;
		color: #777;
	}
}
fieldset {
	border: 0;
	padding: 0;
	margin-bottom: 45px;
}
label {
	width: 25%;
	display: inline-block;
	float: left;
	text-transform: uppercase;
	padding: 10px 0;
}
legend {
	color: $color__primary;
	text-transform: uppercase;
	font-weight: bold;
	display: block;
	width: 100%;
	margin-bottom: 15px;
	font-size: 1.1rem;
}
input[type='text'],
input[type='email'],
input[type='password'],
input[type='search'],
select,
textarea {
	width: 75%;
	float: left;
	border: 1px solid #000;
	background: #101010;
	color: #777;
	padding: 7px 10px;
	border-radius: 4px;
	.input-2 & {
		width: 48%;
	}
}
.input-2 {
	width: 75%;
	display: flex;
	justify-content: space-between;
	float: left;
}
.input-wrap {
	margin-bottom: 15px;
	float: left;
	width: 100%;
	&.error {
		input {
		    border: 1px solid red;
			box-shadow: 0 0 7px red;
		}
	}
}
.form-note {
	border: 1px solid #1a1a1a;
	padding: 20px;
	background: #222;
	font-style: italic;
	float: left;
	width: 100%;
	color: $color__mid;
	border-radius: 5px;
	opacity: 0;
	transition: opacity 0.4s;
	transition-delay: 0.6;
	display: none;
	&.show {
		opacity: 1;
		display: block; 
	}
	.icon {
		width: 20px;
		fill: $color__primary;
		vertical-align: -5px;
		margin-right: 5px;
	}
}
.shipping-address .fields-wrap {
	transition: max-height 0.5s;
	max-height: 0;
	overflow: hidden;
	&.expanded {
		max-height: 500px;
	}
}
#footer {
	position: relative;
	z-index: 3;
	padding-top: 50px;
	padding-bottom: 25px;
	background-color: #000;
	font-family: "Titillium Web",sans-serif;
	font-weight: 300;
	.copyright {
		margin-right: 35px;
		span {
			font-weight: 700;
		}
	}
	.left-side {
		float: left;
	}
	.right-side {
		float: right;
	}
	.soc-networks {
		margin: 0;
	}
	.to-top {
		display: inline-block;
		margin-left: 25px;
		position: relative;
		bottom: 17px;
		vertical-align: top;
		text-align: center;
		color: #808080;
		text-transform: uppercase;
		font-size: 10px;
		cursor: pointer;
		&:hover {
			color: $chrome;
		}
		i {
			font-size: 3.4em;
		}
	}
}
.footer-nav {
	display: inline-block;
	vertical-align: top;
	ul {
		li {
			float: left;
			text-transform: uppercase;
			&:not(:first-child) {
				margin-left: 10px;
			}
			a {
				color: #a6a6a6;
				&:hover {
					color: $chrome;
				}
			}
		}
	}
}
@media only screen and (max-width: 991px) {
	#footer {
		.copyright {
			margin-right: 0;
			display: block;
			text-align: center;
		}
		.left-side, .right-side {
			float: none;
		}
		.right-side {
			margin-top: 15px;
			text-align: center;
			.soc-networks {
				float: none;
				ul {
					display: inline-block;
				}
			}
			.to-top {
				margin-left: 0;
				margin-top: 10px;
				position: static;
			}
		}
		.footer-nav {
			display: block;
			ul {
				width: 100%;
				text-align: center;
				li {
					float: none;
					margin-left: 0!important;
					margin-top: 10px;
				}
			}
		}
	}
}
@media only screen and (max-width: 767px) {
	#fixed-bar {
		.soc-networks {
			display: none;
		}
	}
	#header .soc-networks {
		display: none;
	}
	#logo {
		margin-left: 0;
		left: 15px;
	}
}
@media only screen and (max-width: 479px) {
	body {
		font-size: 14px;
	}
	#logo {
		top: 23px;
		a {
			width: 60px;
			height: 60px;
			background-size: 60px 60px;
		}
	}
	#main-nav ul li.dd-menu {
		margin-left: 15px;
	}
	#main-nav ul li.dd-menu ul {
		right: 0;
		left: -50%;
	}
}
@import 'col-left';
@import 'col-right';